@use "../../Styles/Constants.scss";

.contact-page {
    p {
        padding-bottom: 2rem;
    }

    a {
        color: black;
        padding-right: 2rem;
        margin-right: 2rem;
        font-size: 2rem;
    }

    a:hover {
        box-shadow: inset 0 -1rem 0 0 Constants.$primary-color;
    }
}

@media (max-width: 425px) {
    .contact-page {
        p {
            margin-top: 0;
            text-align: center;
        }

        .contact-icons {
            display: flex;
            justify-content: space-evenly;
            a {
                padding: 0 .5rem;
                margin-right: 0;
            }
            
        }
    }
}